<script>
export default {
	name: "ProductDetail",
  components: {
      topsubmenu: require('../components/topmenu.vue')
  },
	data(){
		return {
      detail:{

      },
      pid:0,
      commentlist:[],
      commentcount:0,
      commentpage:0,
      detailMode:"detail",
      addCartImg: false
		}
	},
  filters: {  
    replace: function (value) {  
      return value.replace('{0}', 'T420X420_')
    },
  },
	mounted(){
    this.$store.commit('changeMenu', false);
    this.$store.commit('changeNav', "ProductDetail");
    this.init();
	},
	methods: {
    //
    goback:function(){
      window.history.go(-1)
    },
    //
    init:function(){
      this.pid = this.$route.params.productid;
      //
      this.getProductData();
      this.getproductComment();
    },
    //
    getProductData:function(){
      this.$http.post(this.$store.state.apidomain + "m/app/ProductDetail", {
        pId: this.pid
      }).then(function(respone){
        this.detail = respone.body.result;
        this.detail.xmltext = this.detail.xmltext.split('/ueditor/').join('http://www.guyushi.com/ueditor/');

        this.$store.commit('changeTitle', this.detail.name);
      }, function(respone){

      })
    },
    //
    getproductComment:function(){
      this.commentpage++;
      this.$http.post(this.$store.state.apidomain + "m/app/productCommon", {
        pId: this.pid, page:this.commentpage
      }).then(function(respone){
        if (respone.body.result == null) {
          return;
        }
        if (this.commentlist == null) this.commentlist = [];
        for (var i = respone.body.result.commonlist.length - 1; i >= 0; i--) {
          this.commentlist.push(respone.body.result.commonlist[i])
        }
        
        this.commentcount = respone.body.result.list_count;
      }, function(respone){
        
      })
    },
    //
    changeDetailSubMenu(title){
      this.detailMode = title;
      //console.info("el"+ this.$el);
      var anchor = this.$el.querySelector("#detailArea");
      //console.info(anchor)
      document.body.scrollTop = anchor.offsetTop;
    },
    addcart() {
      //this.addCartImg = true;
      //this.change();
      this.$store.commit("addCart", {
        id:this.detail.sku,
        sku:'',
        count:1,
        name: this.detail.name,
        price: this.detail.saleprice,
        src: this.detail.bigPic[0].replace('{0}', 'T200X200_')
      });

      //this.change();
    }
	}
}
</script>

