.sn-list-box {
    width: 20rem;
    background: #FFF;
    margin-top: 2.4375rem;
}
.sn-list-nav {
    position: fixed;
    left: 0;
    top: 2.4375rem;
    width: 5rem;
    height: calc(100vh - 2.4375rem);
    overflow-y: auto;
    z-index: 50;
    background-color: #f4f4f4;
}.sn-list-nav ul li {
    position: relative;
    float: left;
    width: 5rem;
    height: 1.88rem;
    line-height: 1.88rem;
    text-align: center;
    background: #F4F4F4;
    border-right: 1px solid #e9e9e9;
    border-bottom: 1px solid #e9e9e9;
    box-sizing: border-box;
    font-size: .52rem;
    color: #666;
}.sn-list-nav ul li.cur {
    border-right-color: transparent;
    background: #FFF;
    font-size: .56rem;
    color: #FE256B;
}
.sn-list-nav ul li:before {
    display: block;
    position: absolute;
    left: 0;
    bottom: -1px;
    content: '';
    width: 4.96rem;
    height: 1px;
    background: #E9E9E9;
}.sn-list-nav ul li:after {
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    content: '';
    height: 100%;
    width: .16rem;
}.sn-list-nav ul li.cur:after {
    display: block;
}.sn-list-con {
    box-sizing: border-box;
    margin: 0 0 0 5rem;
    padding: .4rem;
    overflow: auto;
}.sn-list-con .con-box {
    width: 15rem;
}.sn-list-con .con-box .module-title {
    text-align: center;
    margin: 1.2rem auto .8rem auto;
}.sn-list-con .con-box .module-title {
    color: #FE256B;
}.sn-list-con .con-box .module-title:nth-child(1) {
    margin: .8rem auto .8rem auto;
}.sn-list-con .con-box .module-title em {
    display: inline-block;
    position: relative;
    height: 12px;
    line-height: 12px;
    font-size: 12px;
    vertical-align: middle;
    padding: 0 .92rem;
}.sn-list-con .con-box .module-title em:before, .sn-list-con .con-box .module-title em:after {
    position: absolute;
    top: 5px;
    content: '';
    width: .72rem;
    height: 1px;
    background: #f2f2f2;
}.sn-list-con .con-box .module-title em:before {
    left: 0;
}.sn-list-con .con-box .module-title em:after {
    right: 0;
}.sn-list-con .con-box .hot-special {
    width: 10.8rem;
    overflow: hidden;
    padding: 0 .2rem;
}.sn-list-con .con-box .hot-special a {
