<template>
  <div id="search-page" class="page show mach-width">
    <div class="search_head">
        <a id="history_back" @click="goback" class="pull_left">
          <img src="//f0.jmstatic.com/btstatic/h5/common/left_arrow.png" alt="">
        </a>
        <div class="input_outer">
            <img src="//f0.jmstatic.com/btstatic/h5/common/search_btn.png" alt="" class="search_icon">
            <input type="text" class="search_input" autofocus="autofocus"
            @keyup.enter="search" v-model="keywords" placeholder="" id="search_input">
        </div>
        <a class="search_btn" id="search_cancel" @click="search"></a>
    </div>
    <div id="result-list" v-loading="loading"
      element-loading-text="">
      <ul class="recommend-list" v-if="productlists != null">
        <li v-for="(o, index) in productlists.length">
          <a :href="'/#/p/' + productlists[index].id"><img :src="$store.state.domainUrl + productlists[index].pic.replace('{0}', 'T200X200_')" alt=""> 
          <span class="fs14">{{ productlists[index].name }}</span> <span class="color-jm fs18">{{productlists[index].saleprice}}</span>&nbsp; <!----> 
          <p class="fs11 color9 hide">{{productlists[index].salecount}}</p></a>
        </li>
      </ul>
      <div id='loadmore' v-if='this.productlists != null && !loading && pagecount > page' v-on:click='loadmore()'>
        
      </div>

      <div id="noresult" v-if="productlists == null">
        <img src="//f0.jmstatic.com/btstatic/h5/common/no_result.png" alt="">
        <div class="no-result-info"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
	name: "Search",
  //props:['isHide'],
	data(){
		return {
			//Component Data
      keywords:"",
      loading :false,
      page:0,
      productlists:[],
      count: 0,
      pagecount:0
		}
	},
	mounted(){
    var k = this.$route.params.keywords;
    if (k) {
      this.keywords = k;
      this.search();
    }

    this.$store.commit('changeMenu', false);
    this.$store.commit('changeNav', "search");
    this.$store.commit('changeTitle', "");
    window.addEventListener('scroll', this.scrollMore);
	},
	methods: {
    scrollMore(){
      if (this.pagecount > this.page) {
        var scrollTop = window.pageYOffset|| document.documentElement.scrollTop || document.body.scrollTop;
        var scrollHeight = document.body.clientHeight;
        var clientHeight = document.documentElement.clientHeight;

        //
        if(scrollTop + clientHeight >= scrollHeight){
            //DoLoading
          this.page++;
          this.search();
        }
        //
        //else if(scrollTop <= 0){
        //}
      }
    },
    loadmore(){
      this.page++;
      this.search();
    },
    goback:function(){
      window.history.go(-1)
    },
    search:function(){
      if (this.keywords.trim() == '') {
        this.$message.error('');
        return;
      }
      if (this.loading) {
        return;
      }

      this.loading = true;
      //
      this.$http.post(this.$store.state.apidomain + 'm/app/SearchProductList',{
        cid:0,brandid:0,keyword:this.keywords,
        page: this.page
      }).then(function(response) {
        var response1 = response.body; 
        if (response1.status == 'success' && response1.result != null) {
          this.count = response1.result.data.list_count;
          this.pagecount = this.count/10;
          if (this.productlists == null) this.productlists = [];
          for (var i = response1.result.data.productlist.length - 1; i >= 0; i--) {
            this.productlists.push(response1.result.data.productlist[i])
          }
        } else {
          this.productlists = null;
        }

        this.loading = false;
      }, function (response) {
        this.loading = false;
      });
    }
	}
}
</script>
<style>
#result-list{
  width:100%;
  z-index: 1000;
  margin-bottom: 6rem;
}
#noresult{
  text-align: center;
}
  #search-page{
    /*position: absolute;
    top:0;*/
    background:#fff;
    width:100%;
    z-index:1000;
    max-width: 480px;
    margin: 0 auto;
  }
  .search-page footer{
    display: none
  }
  .search_head {
      border-bottom: solid .0625rem #f5f5f5;
      background: #fff;
      position: relative;
      padding: .347rem 3.093rem .347rem .8rem;
      margin-bottom: 10px;
  }
  .mach-width .search_head {
    padding: .347rem 2.88rem .347rem 2.88rem;
    border-bottom: solid .0625rem #eee;
}
.pull_left {
    float: left;
}
#history_back {
    position: absolute;
    top: .75rem;
    left: 1.227rem;
    width: .4rem;
    height: .8rem;
    overflow: hidden;
    display: inline; transform-origin: 0px 0px 0px; opacity: 1;
}
#search-page a {
    text-decoration: none;
    color: #333;
    display: block;
}#history_back img {
    width: 100%;
}
.input_outer {
    border-radius: 1.25rem;
    -webkit-border-radius: 1.25rem;
    -moz-border-radius: 1.25rem;
    background: #F5F5F5;
    position: relative;
    box-shadow: 0 0.0625rem 0.125rem rgba(0,0,0,.05) inset;
}.mach-width .input_outer {
    width: 14.24rem;
}.search_icon {
    width: .773rem;
    height: .773rem;
    position: absolute;
    top: .427rem;
    left: .853rem;
}.search_input {
    border: none;
    background: 0 0;
    padding-left: 1.866rem;
    color: #999;
    width: 12.374rem;
    height: 1.653rem;
}
#search-page .search_btn {
    line-height: 14px;
}#search-page .search_btn {
    color: #999;
    position: absolute;
    top: .773rem;
    right: .64rem;
}
.proinfo{
  padding:14px;
}
.proinfo p{
  width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.recommend-list{padding-left:.26667rem;overflow:hidden}.recommend-list>li{width:9.6rem;background-color:#fff;margin-right:.26667rem;margin-bottom:.26667rem;float:left;padding:.48rem;-webkit-box-sizing:border-box;box-sizing:border-box;border-radius:4px;-webkit-webkit-box-shadow:0 2px 4px rgba(0,0,0,.05);-moz-webkit-box-shadow:0 2px 4px rgba(0,0,0,.05);-o-webkit-box-shadow:0 2px 4px rgba(0,0,0,.05);-webkit-box-shadow:0 2px 4px rgba(0,0,0,.05);box-shadow:0 2px 4px rgba(0,0,0,.05)}.recommend-list>li>a>img{width:8.64rem;height:8.64rem;border:0;display:block;margin-bottom:8px}.recommend-list span.fs14{overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;height:32px;line-height:16px;margin-bottom:10px}.recommend-list del{text-decoration:line-through}.recommend-list p.f11{margin-top:8px}.more-box{width:100%;line-height:1.92rem}.more-recommend{margin:0 auto;margin-top:5px;width:146px;padding-left:14px;background:url(data:image/png;base64,) no-repeat left 15px;background-size:9px 5px}
</style>



